
.wp {
  border-top: thin solid #ddd;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
  -ms-flex-wrap: nowrap;
      flex-wrap: nowrap;
}
.wp > * {
  font-size: 1em;
  background: #efefef;
  -webkit-box-flex: 0;
      -ms-flex-positive: 0;
          flex-grow: 0;
  /* do not grow   - initial value: 0 */
  -ms-flex-negative: 0;
      flex-shrink: 0;
  /* do not shrink - initial value: 1 */
  -ms-flex-preferred-size: 40px;
      flex-basis: 40px;
  /* width/height  - initial value: auto */
  overflow: hidden;
  padding: 2px;
  text-align: right;
}
.wp > *:nth-child(2n) {
  background: white;
}
.day {
  border-right: thin solid #ccc;
}
.day-header {
  font-size: .75em;
}
.activity {
  background: white;
  text-shadow: 0 0 0.5em rgba(0, 0, 0, 0.25);
  margin: 0 0 .5em;
  padding: 0;
  border: thin solid #dedede;
  font-size: 12px;
}
.activity-header {
  border-bottom: solid thin #dedede;
  margin-bottom: .5em;
}
.activity-header small {
    padding-left: .2em;
    color: #999999;
    font-weight: 100;
    font-size: .8em;
}
.wp > .weekend {
  background: #aaa !important;
  text-shadow: -1px 1px 0 white;
}
.wp > .samedi {
  background: #ccc !important;
}
.activity-title {
  margin: 0;
  padding: 0;
  font-weight: 300;
  color: #000000;
  font-size: 18px;
  line-height: 1.25em;
}
.activity-title .project-acronym {
    font-weight: 400;
    background: #999;
    color: #fff;
    padding-right: 1em;
    position: relative;
}
.activity-title .project-acronym:after {
      content: '';
      display: block;
      background: #999;
      -webkit-transform: rotate(45deg);
          -ms-transform: rotate(45deg);
              transform: rotate(45deg);
      width: 1em;
      height: 1em;
      position: absolute;
      right: -.2em;
      top: .2em;
}
.activity-title .activity-label {
    padding-left: .25em;
}



